.nav {
	&-w {
		// display: flex
		display: grid;
		grid-template-columns: repeat(3,1fr );
		padding: 42px 0;
		align-items: center;
		gap: 10px;
		@include media-breakpoint-down(md) {
 } }			// display: flex

	&-logo {
		margin: 0 auto;
		&__mobile {
			display: none; }
		img {
			max-width: 140px;
			height: auto; } }
	&-auth {
		display: flex;
		justify-content: flex-end;
		min-width: 178px;
		height: 60px;
		align-items: center; }
	&-burger {
		min-width: 30px;
		width: 30px;
		height: 35px;
		display: flex;
		align-items: center;
		display: none;
		&.is-close {
			span {
				background: rgba($red, 0);
				&:before {
					transform: rotate(-45deg);
					top: 0; }
				&::after {
					transform: rotate(45deg);
					top: 0; } } }
		span {
			height: 3px;
			width: 100%;
			background: $red;
			position: relative;
			border-radius: 3px;
			transition: all .25s ease;
			&:before, &:after {
				position: absolute;
				content: '';
				width: 100%;
				height: inherit;
				background: $red;
				border-radius: inherit;
				left: 0;
				transition: all .25s ease; }
			&:before {
				top: -7px; }
			&:after {
				top: 7px; } } }
	&-search {
		&__show, &__close {
			display: none; } }
	&-login {
		display: flex;
		align-items: center;
		overflow: hidden;
		&:hover {
			@include media-breakpoint-up(lg) {
				.nav-login {
					&__avatar {
						left: calc(100% - 60px); }
					&__body {
						.btn {
							opacity: 1;
							left: calc(100% - 80px);
							pointer-events: all;
							transform: translateX(-100%);
							transition-delay: 0.1s; } }
					&__name {
						opacity: 0;
						transform: translateX(100%); } } } }
		&__avatar {
			border-radius: 50%;
			overflow: hidden;
			max-width: 60px;
			min-width: 60px;
			position: relative;
			transition: all .25s ease;
			left: 0;
			div {
				width: 100%;
				padding-bottom: 100%;
				border-radius: inherit;
				position: relative;
 }				// background: $darkred
			img {
				width: 100%;
				height: auto;
				border-radius: inherit;
				border: 2px solid #48494b;
				position: absolute;
				top: 0;
				left: 0; } }
		&__name {
			margin-left: 20px;
			font-size: 14px;
			transition: all .25s ease;
			min-width: 90px; }
		&__body {
			position: relative;
			height: 100%;
			display: flex;
			align-items: center;
			.btn {
				position: absolute;
				opacity: 0;
				pointer-events: none;
				left: -100%; } } } }
@include media-breakpoint-down(md) {
	.nav {
		&-w {
			padding: 9px 0;
			grid-template-columns: 55px 40px auto 94px;
			height: 80px; }
		&-burger {
			display: flex;
			margin-right: 25px; }
		&-logo {
			&__mobile {
 }				// display: block
			img {
				// max-width: 32px
				&:not(.nav-logo__mobile) {
 } } }					// display: none
		&-search {
			.form {
				position: fixed;
				display: flex;
				width: 100vw;
				height: 100vh;
				align-items: center;
				justify-content: center;
				flex-direction: column;
				inset: 0 0 0 0;
				background: rgba($dark, 0.7);
				transition: all .25s ease;
				opacity: 0;
				pointer-events: none;
				z-index: 3;
				// left: 50%
				// transform: translateX(-50%)
				&.is-show {
					opacity: 1;
					pointer-events: all;
					.form__search, .nav-search__close {
						transition-delay: .25s;
						transform: translateY(0px);
						opacity: 1; } }
				.form__search {
					width: 50%;
					transform: translateY(-50px);
					transition: all .25s ease;
					opacity: 0;
					.form__group {
						width: 100%;
						.form__border {
							transform: scaleX(1); } } } }
			&__show {
				display: flex; }
			&__close {
				display: flex;
				margin-bottom: 3rem;
				opacity: 0;
				transform: translateY(-50px);
				transition: all .25s ease; } }
		&-auth {
			min-width: 94px;
			&>.btn {
				position: relative;
				font-size: 0;
				background: 0 0;
				width: 24px;
				height: 24px;
				padding: 0;
				pointer-events: all;
				&:before {
					display: none; }
				&:after {
					min-width: 100%;
					content: '';
					width: 100%;
					height: 100%;
					position: absolute;
					left: 0;
					top: 0;
					background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" xml:space="preserve" viewBox="0 0 1000 1000" fill="white"><path d="M912.4 44H448.1c-21.2 0-39.3 7.6-54.4 22.7S371 100.1 371 121.6v154.6c0 7.1 2.5 13.1 7.6 18.1 5 5 11.1 7.6 18.1 7.6s13.1-2.5 18.1-7.6c5-5 7.6-11.1 7.6-18.1V121.6c0-7.1 2.5-13.1 7.6-18.1 5-5 11.1-7.6 18.1-7.6h390.3l-197.9 84.6c-17.8 7.7-32.8 20.8-45.1 39.3-12.3 18.5-18.4 37.3-18.4 56.4v490H448.1c-7.1 0-13.1-2.5-18.1-7.6-5-5-7.6-11.1-7.6-18.1V585.4c0-7.1-2.5-13.1-7.6-18.1-5-5-11.1-7.6-18.1-7.6s-13.1 2.5-18.1 7.6c-5 5-7.6 11.1-7.6 18.1v155.1c0 21.2 7.6 39.3 22.7 54.4s33.2 22.7 54.4 22.7H577v77.6c0 11.1 2.3 21.2 6.8 30.2s10.8 16.3 18.9 21.7c4.4 3 9.1 5.3 14.4 6.8 5.2 1.5 10.5 2.3 15.9 2.3 4.7 0 9.3-.5 13.8-1.5s9.1-2.5 13.8-4.5l265.9-114.3c17.8-7.4 32.8-20.3 45.1-38.8 12.3-18.5 18.4-37.3 18.4-56.4V121.8c0-21.5-7.6-39.8-22.7-54.9C952.2 51.5 933.9 44 912.4 44zm25.7 696.5c0 9.1-3.2 18.5-9.6 28.2-6.4 9.7-13.8 16.3-22.2 19.6L640.5 902.6c-2.7 1-4.8 1.5-6.3 1.5h-3.3c-.3-.3-.8-1.3-1.3-2.8-.5-1.5-.8-3.6-.8-6.3V276.1c0-9.1 3.2-18.5 9.6-28.2s13.8-16.5 22.2-20.1l275-117.8c.7 1.7 1.3 3.5 1.8 5.5s.8 4 .8 6v619h-.1zM440.6 412.6 286 258c-5-5-11.1-7.6-18.1-7.6s-13.1 2.5-18.1 7.6c-5 5-7.6 11.1-7.6 18.1s2.5 13.1 7.6 18.1L360.6 405H35.7c-7.1 0-13.1 2.5-18.1 7.6-5 5-7.6 11.1-7.6 18.1s2.5 13.1 7.6 18.1c5 5 11.1 7.6 18.1 7.6h324.8L249.7 567.2c-5 5-7.6 11.2-7.6 18.4 0 7.2 2.5 13.3 7.6 18.4 2.4 2.4 5.1 4.2 8.3 5.5s6.5 2 9.8 2 6.6-.7 9.8-2c3.2-1.3 6-3.2 8.3-5.5l154.6-155.1c5-5 7.6-11.1 7.6-18.1s-2.5-13.1-7.5-18.2z"/></svg>') no-repeat center center / contain;
					z-index: 2; } } }
		&-login {
			&__name {
				display: none; }
			&__body {
				.btn {
					position: relative;
					left: 0;
					opacity: 1;
					font-size: 0;
					background: 0 0;
					width: 24px;
					height: 24px;
					margin-left: 10px;
					padding: 0;
					pointer-events: all;
					&:before {
						display: none; }
					&:after {
						min-width: 100%;
						content: '';
						width: 100%;
						height: 100%;
						position: absolute;
						left: 0;
						background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="none" stroke="white" stroke-linecap="round" stroke-linejoin="round" stroke-width="2" class="icon icon-tabler icon-tabler-logout"><path stroke="none" d="M0 0h24v24H0z"/><path d="M14 8V6a2 2 0 0 0-2-2H5a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h7a2 2 0 0 0 2-2v-2"/><path d="M7 12h14l-3-3m0 6 3-3"/></svg>') no-repeat center center / contain;
						z-index: 2;
						clip-path: none; } } } } } }
@media screen and (max-width:670px) {
	.nav {
		&-burger {
			display: flex;
 }			// margin-left: 15px
		&-logo {
			&__mobile {
				display: block; }
			img {
				max-width: 32px;
				&:not(.nav-logo__mobile) {
					display: none; } } }
		&-login {
			&__avatar {
				min-width: 45px;
				width: 45px; } }
		&-auth {
			min-width: 79px; } } }
@include media-breakpoint-down(xs) {
	.nav {
		&-search {
			.form {
				.form__search {
					width: 100%;
					padding: 0 15px; } } } } }
@media screen and (max-width:380px) {
	.nav {
		&-w {
			grid-template-columns: 55px 32px auto 94px; }
		&-logo {
			img {
				max-width: 23px; } } } }
