.pagination {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    &>li {
        &>* {
            padding: 5px 0px;
            display: block;
            cursor: pointer;
            min-width: 50px;
            text-align: center;
            font-size: 16px;
            color: $darkred;
            // background: #edeef0
            border-radius: 3px;
            margin: 0 3px;
            @include media-breakpoint-down(xs) {
                min-width: 15px;
                font-size: 14px; }
            &:hover {
                color: $red; }
            &.is-active {
               font-weight: 700;
               color: $red; } }
        &>.arrow {
            display: block;
            height: 100%;
            position: relative;
            padding: 0;
            &:before {
                position: absolute;
                content: '';
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                background-image: url('data:image/svg+xml;utf8,<svg viewBox="0 0 18 18" xmlns="http://www.w3.org/2000/svg"><path fill="%23b81d24" d="M11.56 5.56L10.5 4.5 6 9l4.5 4.5 1.06-1.06L8.12 9z"></path></svg>');
                // background-size: 28px 28px
                width: 28px;
                height: 28px; }
            &.is-disabled {
                pointer-events: none; } }
        &:last-child {
            &>.arrow {
                &::before {
                     transform: translate(-50%, -50%) rotate(180deg); } } } } }
