.main {
	&-grid {
		display: grid;
		grid-template-columns: repeat(auto-fill, minmax(240px, 1fr) );
		grid-gap: 25px;
		@include media-breakpoint-down(xs) {
			grid-template-columns: repeat(2,1fr );
			grid-gap: 15px; }
		@media screen and (max-width:375px) {
			grid-gap: 10px; }
		&+.pagination {
			margin-top: 3rem;
			padding-bottom: 3rem; } } }
.card {
	text-align: center;
	display: flex;
	flex-direction: column;
	overflow: hidden;
	position: relative;
	box-shadow: 0 0 20px #000;
	// padding-bottom: 15px
	border-radius: 5px;
	background: rgba($dark, .85);
	&:hover {
		.card__img {
			img {
				transform: scale(1.05); } } }
	&__img {
		// margin-bottom: auto
		overflow: hidden;
		position: relative;
		padding-bottom: 150%;
		-webkit-mask-image: -webkit-gradient(linear, left top, left bottom, color-stop(0, black), color-stop(0.35, black), color-stop(0.5, black), color-stop(0.65, black), color-stop(0.85, rgba(0, 0, 0, 0.6)), color-stop(1, transparent));
		img {
			width: 100%;
			// height: auto
			height: 100%;
			object-fit: cover;
			object-position: top;
			position: absolute;
			top: 0;
			left: 0;
			transition: all .25s ease; } }
	&__name {
		margin-top: auto;
		font-size: 15px;
		line-height: 1.2em;
		min-height: 50px;
		padding: 10px;
		display: flex;
		align-items: center;
		justify-content: center;
		@include media-breakpoint-down(xs) {
			font-size: 14px; } }
	&__score {
		position: absolute;
		top: 0;
		right: 0;
		padding: 3px 5px;
		font-size: 14px;
		z-index: 3;
		color: $text;
		&:before {
			position: absolute;
			content: '';
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			background: $darkred;
			opacity: 0.9;
			z-index: -1; } } }
