.details {
	// background-size: calc(100% - 76px)
	overflow: hidden;
	article {
		margin: 3rem 0;
		&:first-child {
			margin-top: 0; }
		&:last-child {
			margin-bottom: 0; } }
	&-head {
		background-color: #181818;
		background-position: 76px 0;
		background-repeat: no-repeat;
		background-size: cover;
		position: relative;
		&__bg {
			position: absolute;
			width: 100%;
			height: 100%;
			top: 1px;
			// left: 76px
			left: 0;
			padding-left: 76px;
			@include media-breakpoint-down(md) {
				position: relative;
				padding-left: 0; }
			img {
				width: 100%;
				height: 100%;
				object-fit: cover; }
			&:after {
				content: "";
				position: absolute;
				width: 100%;
				height: 100%;
				left: 0;
				top: 1px;
				background: linear-gradient(to top,#181818 0,rgba(23,23,23,.987) 1.62%,rgba(23,23,23,.951) 3.1%,rgba(23,23,23,.896) 4.5%,rgba(23,23,23,.825) 5.8%,rgba(23,23,23,.741) 7.06%,rgba(23,23,23,.648) 8.24%,rgba(23,23,23,.55) 9.42%,rgba(23,23,23,.45) 10.58%,rgba(23,23,23,.352) 11.76%,rgba(23,23,23,.259) 12.94%,rgba(23,23,23,.175) 14.2%,rgba(23,23,23,.104) 15.5%,rgba(23,23,23,.049) 16.9%,rgba(23,23,23,.013) 18.38%,rgba(23,23,23,0) 20%); } } }
	&-wrap {
		display: flex;
 }		// align-items: center
	&-info {
		position: relative;
		width: 35%;
		min-width: 500px;
		max-width: 800px;
		padding: 30px 32px 64px 0;
		z-index: 2;
		height: auto;
		display: flex;
		flex-direction: column;
		justify-content: center;
		@include media-breakpoint-down(md) {
			width: 100%;
			min-width: 0px;
			padding-right: 0;
			padding-bottom: 1rem; }
		.title {
			font-weight: 700;
			margin-bottom: 30px; }
		&:after {
			content: "";
			position: absolute;
			top: 0;
			bottom: 0;
			right: -200px;
			margin-bottom: -1px;
			left: 0;
			z-index: -1;
			background: linear-gradient(to right,#181818 10%,rgba(23,23,23,.98) 20%,rgba(23,23,23,.97) 25%,rgba(23,23,23,.95) 35%,rgba(23,23,23,.94) 40%,rgba(23,23,23,.92) 45%,rgba(23,23,23,.9) 50%,rgba(23,23,23,.87) 55%,rgba(23,23,23,.82) 60%,rgba(23,23,23,.75) 65%,rgba(23,23,23,.63) 70%,rgba(23,23,23,.45) 75%,rgba(23,23,23,.27) 80%,rgba(23,23,23,.15) 85%,rgba(23,23,23,.08) 90%,rgba(23,23,23,.03) 95%,rgba(23,23,23,0) 100%); } }
	&-img {
		padding-bottom: 55%;
		display: flex;
		flex: 1 1 auto;
		flex-direction: row; }
	&-networks {
		display: flex;
		align-items: center;
		gap: 10px;
		position: relative;
		&__item {
			max-width: 70px;
			position: relative;
			z-index: 2;
			padding: 5px;
			&:before {
				position: absolute;
				// content: ''
				width: 100%;
				height: 100%;
				top: 0;
				left: 0;
				background: $darkgray;
				opacity: 0.3;
				z-index: -1; }
			img {
				max-width: 100%;
				height: auto; } } }
	&-metadata {
		display: flex;
		@include media-breakpoint-down(md) {
			flex-wrap: wrap;
			row-gap: 5px; }
		&__item {
			font-weight: 300;
			position: relative;
			line-height: 1em;
			display: flex;
			align-items: center;
			font-size: 14px;
			white-space: nowrap;
			color: #a3a3a3;
			margin-right: 6px;
			&.genres {
				flex-wrap: wrap; }
			span.rating {
				border: 1px solid #a3a3a3;
				padding: 2px 3px;
				display: block;
				line-height: 1em;
				font-size: 12px;
				min-width: 20px;
				text-align: center;
				white-space: nowrap;
				color: #a3a3a3;
				&:after {
					display: none; } }
			&:not(:first-child) {
				padding-left: 6px;
				&:before {
					height: 100%;
					content: '';
					width: 1px;
					display: inline-block;
					background: #a3a3a3;
					margin-right: 6px;
					position: absolute;
					top: 0;
					left: 0; } }
			span {
				font: inherit;
				color: #a3a3a3;
				&:not(:last-child) {
					padding-right: 5px; }
				&:not(:last-child) {
					&:after {
						content: ','; } } } } }
	&-overview {
		margin-top: 1.5rem;
		font-weight: 300;
		font-size: 16px;
		@include media-breakpoint-down(xs) {
			font-size: 14px; }
		&:not(:last-child) {
			margin-bottom: 1rem; } }
	&-credits {
		display: flex;
		font-size: 16px;
		color: #a3a3a3;
		font-weight: 300;
		padding: 4px 0;
		flex-wrap: wrap;
		align-items: center;
		@include media-breakpoint-down(xs) {
			font-size: 14px; }
		&__item {
			color: #fff;
			font-weight: 300;
			padding-right: 5px;
			font-size: 14px;
			&:first-child {
				padding-left: 10px; }
			&:not(:last-child) {
				&:after {
					content: ','; } } } }
	&-btns {
		display: flex;
		gap: 15px;
		margin-top: 2rem;
		flex-wrap: wrap;
		align-self: flex-start;
		justify-content: center; }
	.addToLists {
		// margin-top: 1rem
		position: relative;
		width: 216px;
		.btn {
			width: 100%; }
		&.is-active {
			.lists {
				transform: translateY(0px);
				opacity: 1;
				pointer-events: all; } }
		.lists {
			position: absolute;
			width: 216px;
			top: 48px;
			pointer-events: none;
			transform: translateY(15px);
			opacity: 0;
			transition: all .25s ease;
			border-top: 2px solid $darkred;
			&:before {
				position: absolute;
				content: '';
				width: 10px;
				height: 6px;
				clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
				top: 0px;
				left: 50%;
				background: $darkred;
				transform: translate(-50%, -100%); }
			&>li {
				text-align: center;
				font-size: 16px;
				font-weight: 500;
				padding: 5px 0;
				cursor: pointer;
				background: $darkred;
				&:hover {
					background: darken($darkred, 10%);
					cred {} }
				&+li {
					border-top: 2px solid $dark; }
				&.is-ex {
					pointer-events: none;
					background: #717171; }
				span {
					display: block;
					font-weight: 300;
					font-size: 14px; } } } }
	&-slider {
		&__wrap {
			position: relative;
			@include media-breakpoint-down(xs) {
				// padding: 0 35px
				// margin-left: -15px
				// margin-right: -15px
 }				// max-width: 100vw
			.swiper-container {
				// overflow: visible
				position: unset;
				padding: 10px 0; }
			.swiper-wrapper {} }
		&__container {
 }			// padding: 0 75px !important
		&__item {
			max-width: 500px;
			@include media-breakpoint-down(xs) {
				width: 85%; }
			&:not(.small) {
				transform-style: preserve-3d;
				will-change: transform;
				transform: perspective(800px) rotateY(-.1deg);
				transition: transform 1s ease,-webkit-transform 1s ease;
				&:hover {
					transform: perspective(800px) rotateY(4.5deg); } }
			&.img {
				cursor: pointer; }
			&.small {
				max-width: 290px;
				padding: 20px 0;
				margin-top: -20px;
				@include media-breakpoint-down(xs) {
					width: 70%; } }
			img {
				width: 100%;
 } } } }				// height: auto
