@mixin font-face($font-family, $file-path, $weight: normal, $style: normal) {
	@font-face {
		font-family: 'ProximaNova';
		font-weight: $weight;
		font-style: $style;
		src: url(#{$file-path}.woff) format('woff'); } }

@include font-face("ProximaNova", '../fonts/ProximaNova-Light/ProximaNova-Light', 300);
@include font-face("ProximaNova", "../fonts/ProximaNova/ProximaNova-Regular");
@include font-face("ProximaNova", "../fonts/ProximaNova-LightIt/ProximaNova-LightIt",300, Italic );
@include font-face("ProximaNova", "../fonts/ProximaNova-Bold/ProximaNova-Bold", 700);
@include font-face("ProximaNova", "../fonts/ProximaNova-Semibold/ProximaNova-Semibold", 600);
@include font-face("ProximaNova", "../fonts/ProximaNovaT-Thin/ProximaNovaT-Thin", 100);
